import { Component, OnInit, Input, SimpleChanges, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { NgForm } from '@angular/forms';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-income-add-credit',
  templateUrl: './income-add-credit.component.html',
  styleUrls: ['./income-add-credit.component.scss']
})
export class IncomeAddCreditComponent implements OnInit {
  
  isCreditSubmitted:boolean = false;
  billData:any;

  isError:boolean = false;
  alertMessage:string = "";

  isGeneralCredit:boolean = false;
  isBlockSelected:boolean = false;

  isUnitSelected:boolean = false;

  apartmentBlockId:string = "";
  apartmentBlockUnitId:string = "";

  blocksData:any;
  unitData:any;

  @Input() credit:any;
  @Input() isEdit:boolean;

  @Output() public outputParams = new EventEmitter();

  @ViewChild('addCreditNoteForm') addCreditNoteForm : any;

  constructor(
	private router: Router,
    private route: ActivatedRoute,
	private accountsService:AccountsService,
	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getUnits() {

    let params = {
      apartmentBlockId: parseInt(this.apartmentBlockId)
    }

    this.apartmentService.getApartmentBlockUnitByBlockId(params).subscribe((res:any) => {
        this.isBlockSelected = true;
        this.unitData = res;
    },
    error => {
      console.log(error);
    });

  }
  

  getAllCollection(id) {
	let params = {
		apartmentId: parseInt(this.cookieService.get('apartmentId')),
		apartmentBlockUnitId: id
	}

	this.accountsService.getAllCollectionInvoicesByApartmentBlockUnitId(params).subscribe((res:any) => {
		this.billData = res;
		this.isCreditSubmitted = true;
		this.isUnitSelected = true;
	})
  }

  onSelectChange(event) {
	if(event != null)
	this.credit.collectionId = event.collectionId
  }


  submitAddCreditNoteForm(form){


	if(!this.isEdit) {

  		let details = {

			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
			"blockUnitUserId": null,
			"custCreditNoteId": null,
			"glaccountId": null,
			"invoiceId": this.credit.invoiceId,
			"collectionId": parseInt(this.credit.collectionId),
			"transactionType": 0,
			"amount": parseInt(this.credit.amount),
			"comment": this.credit.comment,
			"active": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString(),
			"updatedBy": null,
			"updatedOn": null

  		}

		let params = {
			custCreditNote: details
		}

  		this.accountsService.addCustCreditNotes(params).subscribe((res:any) => {

	  		if(res.creditNoteId){

		      this.isCreditSubmitted = true;
			  this.sharedService.setAlertMessage("Credit Note added successfully");
			  this.outputParams.emit(true)
				
			  /*this.router.navigateByUrl('/', {skipLocationChange: true}).then(()=>
			  this.router.navigateByUrl('/ams/income/actions/view-creditnote/' + this.route.params['value'].id));*/

		    }
		    else {

		      this.isCreditSubmitted = true;
		      this.isError = true;
		      this.alertMessage = res.errorMessage;

		    }


	  	},
	    error => {
	      this.isCreditSubmitted = true;
	      this.isError = true;
	      this.alertMessage = "Some error occured";
		});
	
	}
	else {

		let details = {

			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
			"blockUnitUserId": this.credit.blockUnitUserId,
			"custCreditNoteId": this.credit.custCreditNoteId,
			"glaccountId": this.credit.glaccountId,
			"invoiceId": this.credit.invoiceId,
			"collectionId": parseInt(this.credit.collectionId),
			"transactionType": this.credit.transactionType,
			"amount": parseInt(this.credit.amount),
			"comment": this.credit.comment,
			"active": this.credit.active,
			"insertedBy": this.credit.insertedBy,
			"insertedOn": this.credit.insertedOn,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString()

  		}

		let params = {
			custCreditNote: details
		}

  		/* this.accountsService.addCustCreditNotes(params).subscribe((res:any) => {


	  		if(res.creditNoteId){

		      this.isCreditSubmitted = true;
		      this.sharedService.setAlertMessage("Credit Note updated successfully");

		    }
		    else {

		      this.isCreditSubmitted = true;
		      this.isError = true;
		      this.alertMessage = res.errorMessage;

		    }


	  	},
	    error => {
	      this.isCreditSubmitted = true;
	      this.isError = true;
	      this.alertMessage = "Some error occured";
		}); */

	}

  }



  ngOnInit() {

	if(this.route.params['value'].id != undefined) {

		this.apartmentBlockUnitId = this.route.params['value'].id
		this.credit.collectionId = "";
		this.getAllCollection(this.route.params['value'].id);
	}
	else {
		
		this.credit = {};
		this.credit.collectionId = "";

		let params = {
			apartmentId: parseInt(this.cookieService.get('apartmentId'))
		}

		//get apartment blocks
		this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
			
			this.blocksData = res;
			this.isGeneralCredit = true;
			this.isCreditSubmitted = true;

		});

	}

  }	

  ngOnChanges(changes: SimpleChanges) {
	this.credit.collectionId = "";
  }
}
