import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { jqxPopoverComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxpopover';

@Component({
  selector: 'app-income-customer-advances',
  templateUrl: './income-customer-advances.component.html',
  styleUrls: ['./income-customer-advances.component.scss']
})
export class IncomeCustomerAdvancesComponent implements OnInit {

  isAdvancesLoaded:boolean = false;

  customerAdvancesData:string = "";
  customerAdvancesDataList:any;

  totalItems:any;

  apartmentBlockUnitId:any;
  apartmentBlockUnitUserId:any;

  advance:any = {};
  isEditAdvance:boolean = false;

  columnData:any ;
  gridSourceData:any;
  popOverSelector:any;
  popOverPosition:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @ViewChild('reversePopOver', { static: false }) reversePopOver: jqxPopoverComponent;

  constructor(
    private route: ActivatedRoute,
    public dialog: MatDialog,
    private apartmentService: ApartmentService,
    private accountsService:AccountsService,
    private cookieService:CookieService,
  ) { }


  getPrintParams(event){
    this.datagrid.exportdata(event, 'CustomerAdvancesData');
  }

  onGlSearchFilter() {

    if(this.customerAdvancesData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.customerAdvancesData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getCustomerAdvancesParams(event) {
    this.reversePopOver.close();
    this.getCustomerAdvancesData();
  }

  addCustomerAdvances() {

    this.isEditAdvance = false;

    this.popOverSelector = '#addCustomerAdvancesElement';
    this.popOverPosition = 'bottom'

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 300);
  }

  @HostListener('window:onCustomerAdvances', ['$event.detail'])
  onCustomerAdvances(detail) {

    this.isEditAdvance = true;

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let transactionId = dataRecord.transactionId;

    this.advance = dataRecord;

    this.popOverSelector = '#'+transactionId;
    this.popOverPosition = 'left'

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 300);

  }

  getCustomerAdvancesData() {

    this.isAdvancesLoaded = false;

    let blockUnitParams = {
      apartmentBlockUnitId: this.apartmentBlockUnitId
    }

    this.apartmentService.getAllApartmentBlockUnitUsersByApartmentBlockUnitId(blockUnitParams).subscribe((res:any) => {

      this.apartmentBlockUnitUserId = res[0].apartmentBlockUnitUserId

      let params = {
        blockUnitUserId: this.apartmentBlockUnitUserId,
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }
  
  
      this.accountsService.getAdvanceByApartmentBlockUnitUserId(params).subscribe((res:any) => {
  
        var customerAdvancesDataList = res;
  
        this.gridSourceData = {
          localdata: customerAdvancesDataList,
          datatype: "array"
        }
        
        this.customerAdvancesDataList = new jqx.dataAdapter(this.gridSourceData);
        this.totalItems = customerAdvancesDataList.length;
  
        this.isAdvancesLoaded = true;
        
      }, error => {
        console.log(error);
      });

    })


  }

  ngOnInit(): void {

    this.apartmentBlockUnitId = this.route.params['value'].id

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: 'Transaction Id',
      datafield: 'transactionId',
      width: 220,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Advance Added',
      datafield: 'creditAmount',
      cellsrenderer: cellsrenderer,
      minwidth: 180,
      renderer: columnrenderer
    }, {
      text: 'Comments',
      datafield: 'comments',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Amount paid for bill',
      datafield: 'billId_CreatedAgainst',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Paid for Bill No',
      datafield: 'billId_AdjustedInvoice',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Advance Balance',
      datafield: 'balance',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {

        let elemId = this.customerAdvancesDataList.loadedData[row].transactionId;

        return '<div class="simple-actions"> <a href="javascript:void(0)" class="mr-2" id="' + elemId + '" onClick="editCustomerAdvancesEvent(' + row + ')" ><i class="icon fa fa-pencil edit" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];

    this.getCustomerAdvancesData();

  }

}

let editCustomerAdvancesEvent = row => {
  let event = new CustomEvent('onCustomerAdvances', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editCustomerAdvancesEvent = editCustomerAdvancesEvent;