import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { ConstantsService } from '../../../../shared/services/constants.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-income-journal',
  templateUrl: './income-journal.component.html',
  styleUrls: ['./income-journal.component.scss']
})
export class IncomeJournalComponent implements OnInit {

  blockData:any;
  blockUnitData:any;

  report:any;
  
  glAccountsDataList:any;
  glDocumentTypeDataList:any;

  glAccountIndicator:any = "Cust_Invoice"

  isBlockSelected:boolean = false;
  isUnitSelected:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 8;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isReportSubmitted:boolean = true;
  isDataLoaded:boolean = true;

  reportsDataList:any;
  reportsData:any;

  apartmentBlockUnitId:any;

  columnData:any ;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private route: ActivatedRoute,
  	private apartmentService:ApartmentService,
    private accountsService:AccountsService,
    private constantsService:ConstantsService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }


  getUnits(id){

    let params = {
      apartmentBlockId: parseInt(id)
    }

  	this.apartmentService.getApartmentBlockUnitByBlockId(params).subscribe((res:any) => {
      	this.blockUnitData = res;
        this.isBlockSelected = true;
    });

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'JournalData');
  }

  onGlSearchFilter() {

    if(this.reportsData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.reportsData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  
  getDate(date){
    return moment(date).format(this.constantsService.dateFormat)
  }

  getDateFormat(date){
    return moment(date).format("YYYY-MM-DD");
  }


  submitIncomeReportsForm(form) {

  	this.isReportSubmitted = true;
    this.isDataLoaded = false;
   
  	let details = {
  	  BlockID: null,
  	  ApartmentBlockUnitId: parseInt(this.apartmentBlockUnitId),
  	  DocumentdateFrom: null,
  	  DocumentdateTo: null,
      PostingdateFrom: this.getDateFormat(this.report.PostingdateFrom),
      PostingdateTo: this.getDateFormat(this.report.PostingdateTo),
      GLaccountID: null,
      GLDocumentTypeID: null, 
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }


  	this.accountsService.getCustGltransactionsMultiFilter(details).subscribe((res:any) => {

      var reportsDataList = res;

      this.gridSourceData = {
        localdata: reportsDataList,
        datatype: "array"
      }
      
      this.reportsDataList = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = reportsDataList.length;

      console.log(reportsDataList);

      this.isDataLoaded = true;
      this.isReportSubmitted = false;

    },
    error => {
      
    });

  }

  ngOnInit() {

  	this.report = {};
    this.apartmentBlockUnitId = parseInt(this.route.params['value'].id);
    
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: 'Customer Invoice Id',
      datafield: 'custInvoiceId',
      width: 180,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Block No',
      datafield: 'apartmentBlock',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Unit No',
      datafield: 'apartmentBlockUnitNumber',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Credit Amount',
      datafield: 'creditAmount',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Debit Amount',
      datafield: 'debitAmount',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Gl Account Name',
      datafield: 'glaccountname',
      width: 180,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Document Date',
      datafield: 'documentDate',
      width: 180,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Entered By',
      datafield: 'enteredByName',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Entry Date & Time',
      datafield: 'entryDateTime',
      width: 180,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateTimeFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Posting Date',
      datafield: 'postingDate',
      width: 180,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Clearing Date',
      datafield: 'clearingDate',
      width: 180,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Clearing Document No',
      datafield: 'clearingDocumentNumber',
      width: 180,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }
    ]

  	this.report.GLaccountID = "";
    this.report.GLDocumentTypeID = "";
    
    let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res:any) => {
      this.blockData = res;
    });

  	 this.accountsService.getAllGlAccounts().subscribe((res:any) => {
      
      this.glAccountsDataList = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator ==  this.glAccountIndicator;
      });


    });
    
    let glDocParams = {
      LookupTypeId: 31
    }

    //Gl Document 
    this.lookupService.getLookupValueByLookupTypeId(glDocParams).subscribe((res:any) => {
      this.glDocumentTypeDataList = res;
    },
    error => {
      
    });

  }

}
