import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { jqxPopoverComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxpopover';

@Component({
  selector: 'app-income-security-deposit',
  templateUrl: './income-security-deposit.component.html',
  styleUrls: ['./income-security-deposit.component.scss']
})
export class IncomeSecurityDepositComponent implements OnInit {

  isDepositLoaded:boolean = false;

  securityDepositData:string = "";
  securityDepositDataList:any;

  totalItems:any;

  apartmentBlockUnitId:any;
  apartmentBlockUnitUserId:any;

  deposit:any = {};
  isEditDeposit:boolean = false;

  columnData:any ;
  gridSourceData:any;
  popOverSelector:any;
  popOverPosition:any;
  popoverWidth:any;
  popoverOffset:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @ViewChild('reversePopOver', { static: false }) reversePopOver: jqxPopoverComponent;

  constructor(
    private route: ActivatedRoute,
    public dialog: MatDialog,
    private accountsService:AccountsService,
    private cookieService:CookieService
  ) { }


  getPrintParams(event){
    this.datagrid.exportdata(event, 'SecurityDepositData');
  }

  onGlSearchFilter() {

    if(this.securityDepositData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.securityDepositData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getSecurityDepositParams(event) {
    this.reversePopOver.close();
    this.getSecurityDepositData();
  }

  addSecurityDeposit() {

    this.isEditDeposit = false;

    this.popOverSelector = '#addSecurityDepositElement';
    this.popOverPosition = 'bottom'
    this.popoverWidth = '600'

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 300);
  }

  @HostListener('window:onSecurityDeposit', ['$event.detail'])
  onSecurityDeposit(detail) {

    this.isEditDeposit = true;

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let transactionId = dataRecord.transactionId;

    this.deposit = dataRecord;

    this.popOverSelector = '#'+transactionId;
    this.popOverPosition = 'left'
    this.popoverWidth = '200'

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 300);

  }

  getSecurityDepositData() {

    this.isDepositLoaded = false;

    let params = {
      blockUnitId: this.apartmentBlockUnitId,
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }


    this.accountsService.getSecurityDepositByApartmentBlockUnitId(params).subscribe((res:any) => {

      var securityDepositDataList = res;

      this.gridSourceData = {
        localdata: securityDepositDataList,
        datatype: "array"
      }
      
      this.securityDepositDataList = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = securityDepositDataList.length;

      this.isDepositLoaded = true;
      
    }, error => {
      console.log(error);
    });

  }

  ngOnInit(): void {

    this.apartmentBlockUnitId = parseInt(this.route.params['value'].id)

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: 'Transaction Id',
      datafield: 'transactionId',
      width: 220,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Security Deposit Paid',
      datafield: 'creditAmount',
      cellsrenderer: cellsrenderer,
      minwidth: 180,
      renderer: columnrenderer
    }, {
      text: 'Deposit Reason',
      datafield: 'comments',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Amount Dedcuted/Used',
      datafield: 'debitAmount',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Reason for Deduction',
      datafield: 'comments2',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Secuirty Deposit Balance',
      datafield: 'balance',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {

        let elemId = this.securityDepositDataList.loadedData[row].transactionId;
        console.log(elemId);

        return '<div class="simple-actions"> <a href="javascript:void(0)" class="mr-2" id="' + elemId + '" onClick="editSecurityDepositEvent(' + row + ')" ><i class="icon fa fa-pencil edit" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];

    this.getSecurityDepositData();

  }

}

let editSecurityDepositEvent = row => {
  let event = new CustomEvent('onSecurityDeposit', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editSecurityDepositEvent = editSecurityDepositEvent;